import React, { useState } from "react";

export default function Navbar() {
  const [showPricing, setShowPricing] = useState(false);

  const handleClick = () => {
    alert("button clicked");
  };

  const handlePricingClick = (e) => {
    e.preventDefault();
    setShowPricing(!showPricing);
  };

  const Pricing = () => {
    return (
      <div>
        <h2>Pricing Information</h2>
        <p>
          Check out our <a href="https://gradtech.in">pricing page</a> for more
          details.
        </p>
      </div>
    );
  };

  return (
    <div>
      <nav className="navbar navbar-expand-lg bg-body-light">
        <div className="container-fluid">
          <a className="navbar-brand" href="#ds">
            <img
              src="https://www.awignexpert.com/images/login/logo.svg"
              alt=""
              style={{ height: "20px" }}
            />
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav">
              <li className="nav-item">
                <a className="nav-link active" aria-current="page" href="#fd">
                  Home
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="#features" onClick={handleClick}>
                  Features
                </a>
              </li>
              <li className="nav-item">
                <a
                  className="nav-link"
                  href="#pricing"
                  onClick={handlePricingClick}
                >
                  Pricing
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
      {showPricing && <Pricing />}
    </div>
  );
}
