import React from "react";
import { PieChart, Pie, Cell, Tooltip, Legend } from "recharts";
import "./ChartCss.css";

const data = [
  { name: "School", value: 70 },
  { name: "College", value: 76 },
  { name: "Award", value: 7 },
  { name: "Lafda", value: 10 },
  { name: "Coporate", value: 5 },
  { name: "rporate", value: 15 },
  { name: "Corate", value: 45 },
  { name: "rate", value: 80 },
];

const COLORS = [
  "purple",
  "blue",
  "91DDCF",
  "cyan",
  "green",
  "dark",
  "gold",
  "FF7D29",
];

export default function Chart() {
  return (
    <PieChart width={600} height={600} margin={{ top: 40 }}>
      <Pie
        data={data}
        innerRadius={40}
        outerRadius={167}
        paddingAngle={4}
        cornerRadius={203}
        startAngle={-801}
        endAngle={80}
        cx={300}
        cy={300}
        dataKey="value"
      >
        {data.map((entry, index) => (
          <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
        ))}
      </Pie>
      <Tooltip />
      <Legend />
    </PieChart>
  );
}
