import React, { useState } from "react";
import "./App.css";
import Navbar from "./component/Navbar.jsx";
import EventHandler from "./component/EventHandler.jsx";
import Uiverse from "./component/Uiverse.jsx";
import Chart from "./component/Chart.jsx";
import State from "./component/State.jsx";
import EditableState from "./component/EditableState.jsx";
import Lifecycle from "./component/Lifecycle.jsx";
import FormEvent from "./component/FormEvent.jsx";
import EventBubbling from "./component/EventBubbling.jsx";
import Hoc from "./component/Hoc.jsx";
import UseStateComp from "./component/Hooks/UseStateCom.jsx";
import HookCounter from "./component/Hooks/HookCounter.jsx";
import HookCounterTwo from "./component/Hooks/HookCounterTwo.jsx";

function App() {
  const [show, setShow] = useState(true);

  return (
    <div className="container">
      <Navbar />
      <br />
      <EventHandler />
      <Uiverse />
      <State />
      <EditableState />
      <Chart />

      <div id="products">
        <button
          className="btn btn-outline-info mt-5"
          onClick={() => {
            setShow(!show);
          }}
        >
          Toggle Product List
        </button>

        {show && <Lifecycle />}
      </div>

      <FormEvent />
      <EventBubbling />
      <Hoc text="" />
      <HookCounterTwo />
      <UseStateComp />
      <HookCounter />
    </div>
  );
}

export default App;
