import React, { Component } from "react";
import "./LApp.css";
export default class Lifecycle extends Component {
  constructor(props) {
    super(props);
    console.log("initialize");
    this.state = {
      products: [],
      counter: 0,
    };
  }

  intervalId = null;

  getData = async () => {
    try { 
      const fetchResponse = await fetch("https://fakestoreapi.com/products");
      const jsonData = await fetchResponse.json();
      this.setState({ products: jsonData });
    } catch (error) {
      console.error("Error fetching data: ", error);
    }
  };

  componentDidMount() {
    this.getData();
    console.log("component did mount");
    this.intervalId = setInterval(() => {
      console.log(Date.now());
    }, 2000);
  }

  componentWillUnmount() {
    clearInterval(this.intervalId);
    console.log("component will unmount");
  }

  componentDidUpdate(prevProps, prevState) {
    console.log("prevProps", prevProps);
    console.log("prevState", prevState);
    console.log("component did update called");
  }

  increment = () => {
    this.setState((prevState) => ({ counter: prevState.counter + 1 }));
  };

  render() {
    console.log("render function called");

    return (
      <>
        <h1>Lifecycle {this.state.counter}</h1>
        <button onClick={this.increment} className="btn btn-primary">
          Increment
        </button>
        <div id="products">
          <ul className="containerproduct">
            {this.state.products.map((product, index) => (
              <li key={index}>
                <span>Product Name: {product.title}</span>
                <br />
                <br />
                <img
                  src={product.image}
                  alt={product.title}
                  height={180}
                  width={180}
                />
                <span>Product Price: {product.price}</span>
                <br />
                <button className="btn" style={{ marginTop: 30 }}>
                  Buy now
                </button>
              </li>
            ))}
          </ul>
        </div>
      </>
    );
  }
}
