import React, { Component } from "react";

export default class UseStateCom extends Component {
  constructor(props) {
    super(props);

    this.state = {
      count: 0,
    };
  }

  incrementCount = () => {
    this.setState({
      count: this.state.count + 1,
    });
  };
  render() {
    return (
      <>
        <button onClick={this.incrementCount} className="btn btn-Danger">
          Count:  {this.state.count}
        </button>
      </>
    );
  }
}
